  </div>
</template>

<script>
import anime from 'animejs';

export default {
  name: 'HelloWorld',

  data() {
    return {
      files: [
        'sinkane-the-searching.mp3',
        'ryewhiskey.mp3',
        '3.mp3',
      ],
      animation: anime.timeline({
          easing: 'linear',
          direction: 'alternate',
          loop: true
      }),
      isPlay: false,
    }
  },

  mounted() {

    this.animation
      .add({
          targets: this.$refs.skull,
          translateY: -4,
      }, 0)
      .add({
          targets: this.$refs.fox,
          translateY: -5,
      }, 100)
      .add({
          targets: this.$refs.bump,
          rotate: '-2deg',
          translateY: 2,
          duration: 500,
          easing: 'linear',
      }, 0)
      .add({
          targets: this.$refs.bump,
          rotate: '1deg',
          translateY: -7,
          duration: 500,
          easing: 'linear',
      }, 500)
      .add({
          targets: this.$refs.car,
          translateY: -3,
          easing: 'linear',
          // duration: 500,
      }, 0)
      .add({
          targets: this.$refs.cap,
          translateY: 2,
          easing: 'linear',
          // duration: 500,
      }, 0)
      .add({
          targets: this.$refs.wheel1,
          translateY: 2,
          translateX: -2,
          easing: 'linear',
          // duration: 500,
      }, 0)
      .add({
          targets: this.$refs.wheel2,
          translateY: 2,
          translateX: -2,
          easing: 'linear',
          // duration: 500,
      }, 0);

      anime({
          targets: this.$refs.line,
          strokeDashoffset: -80,
          easing: 'linear',
          duration: 500,
          loop: true,
      });

      // -80


    this.$nextTick(() => {
      this.animation.pause();
      let promise = this.$refs.audio.play();

      if (promise !== undefined) {
        promise.then(_ => {
            this.animation.play();
            this.isPlay = true;
            
        }).catch(error => {
          isPlay = false;
          // this.animation.play();
            // Autoplay was prevented.
            // Show a "Play" button so that user can start playback.
        });
      }
    })
  },

  computed: {
    currentTrack() {
      let day = (new Date() - new Date(2019, 5, 21)) / 86400000;
      let dayIndex = Math.floor(day);
      return this.files[dayIndex];
    }
  },

  methods: {
    play() {
      let promise = this.$refs.audio.play();

      if (promise !== undefined) {
        promise.then(_ => {
            this.animation.play();
            this.isPlay = true;
            anime({
                targets: this.$refs.sound,
                opacity: {
                  value: [0, 1, 0],
                  duration: 800,
                },
                translateY: -15,
                easing: 'linear',
                duration: 1000,
                loop: true,
                // duration: 500,
            })
        }).catch(error => {
          isPlay = false;
          // this.animation.play();
            // Autoplay was prevented.
            // Show a "Play" button so that user can start playback.
        });
      }
    },

    pause() {
      this.isPlay = false;
      this.$refs.audio.pause();
      this.animation.pause();
    }
  }
}
</script>

